	module.exports = " <div class=blog> <div v-if=$loadingRouteData class=loader> <div class=loader__item></div> <div class=loader__title>Chargement</div> </div> <article class=post v-if=!$loadingRouteData> <header class=post__header> <h1 class=post__title>{{ title }}</h1> <div class=post__infos> <div class=post__date>Post le <time>{{ date | moment 'ddd Do MMM YYYY' }}</time></div> <div class=flex> <div class=post__tags> <span class=c-tag v-for=\"tag in tags\">{{ tag }}</span> </div> <div class=post__share> <module-share></module-share> </div> </div> </div> </header> <div class=post__content v-linkable>{{{ content }}}</div> <footer class=post__footer> <div class=flex> <div class=post__tags> <span class=c-tag v-for=\"tag in tags\">{{ tag }}</span> </div> <div class=post__share> <module-share></module-share> </div> </div> <div class=post__help> Un problme ? Une question ? <br>Vous pouvez me contacter sur <a href=https://www.twitter.com/EmmanuelBeziat target=_blank>Twitter</a>, poster un ticket sur <a href=https://github.com/EmmanuelBeziat/emmanuelbeziat/issues target=_blank>Github</a>, ou bien crer un sujet sur un forum dentraide comme <a href=https://zestedesavoir.com/ target=_blank>ZesteDeSavoir</a>, <a href=https://openclassrooms.com/dashboard target=_blank>OpenClassrooms</a>, <a href=http://www.alsacreations.com/ target=_blank>Alsacrations</a> </div> </footer> </article> <div class=comments v-if=!$loadingRouteData v-if=disqus> <disqus shortname=emmanuelbeziat></disqus> </div> </div> ";

