module.exports = {

  /**
   * Redirects
   * @type {Object}
   */
  redirects: {
  },

  /**
   * Routes
   * @type {Object}
   */
  routes: {
    '/': {
      name: 'home',
      title: 'Emmanuel Bziat',
      component: require('../components/Home')
    },

    '/portfolio': {
      name: 'portfolio',
      title: 'Portfolio  Emmanuel Bziat',
      component: require('../components/PortfolioPostList')
    },

    '/portfolio/:slug': {
      name: 'portfolio-post',
      component: require('../components/PortfolioPost')
    },

    '/blog': {
      name: 'blog',
      title: 'Blog  Emmanuel Bziat',
      component: require('../components/BlogPostList')
    },

    '/blog/:slug': {
      name: 'blog-post',
      component: require('../components/BlogPost')
    },

    '*': {
      name: 'not-found',
      title: 'Page non trouve !',
      component: require('../components/NotFound')
    }
  }
}



