<template>
  <section class="not-found">
    <div class="not-found__image {{ image }}"></div>
    <div class="not-found__404">{{ title }}</div>

    <h1 class="not-found__title">Page introuvable</h1>
    <div class="not-found__content">
      <p>Et voil, vous avez tout cass Et qui cest qui doit rparer tout a maintenant ?</p>
      <p>Oooh, mais cest pas grave, hein ? Ya toujours un informaticien pour tout rgler, pas vrai ?! <br>Aprs tout, cest mon mtier, alors a na rien de compliqu En plus cest mme pas un vrai mtier : cest lordinateur qui fait tout.</p>
      <p>Pfff</p>
    </div>
  </section>
</template>

<script>
const erreurs = {
  images: ['doctor-who', 'the-office', 'south-park', 'mario'],
  titles: ['What?!', 'God please no!', 'Were sorry', 'Page is in another castle']
}

export default {
  data () {
    return {
      title: null,
      image: null
    }
  },

  created: function () {
    this.errorSet()
  },

  methods: {
    errorSet: function () {
      const i = Math.floor(Math.random() * erreurs.images.length)

      this.$set('image', erreurs.images[i])
      this.$set('title', erreurs.titles[i])
    }
  }
}
</script>



